if (process.env.NODE_ENV === 'development' && ENV_MOCK) {
  // require('../mock/example.js');
}

import React from 'react';
import {render} from 'react-dom';
import styles from './index.module.less';
import {Tag, Input} from 'antd';
import Upload from './Upload';

class App extends React.Component {
  state = {
    value: '/'
  }

  onInputChange = e => {
    let {value} = e.target;
    if (value === '') value = '/'
    this.setState({ value });
  }

  render() {
    return (
      <div id={styles.layout}>
        <h1>
          Wherever
          <Tag className={styles.color} style={{marginLeft: 20}}>Upload</Tag>
        </h1>
        <Input addonBefore={<b>上传目录地址</b>} onChange={this.onInputChange} value={this.state.value} />
        <Upload action={`/upload${this.state.value}`} />
      </div>
    )
  }
}

render(
  <App />,
  document.getElementById('root')
);
